header{
    height: 100%;
    padding-top: 6rem;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*-------------------- CvTalk --------------------*/
.cvtalk{
    margin-top: 2rem;
    display: flex;
    gap: 2rem;
    justify-content: center;
}

/*-------------------- Header Socials --------------------*/
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 0.2rem;
}

/*-------------------- Me image --------------------*/
.me{
    width: 22rem;
    height: 30rem;
    border-radius: 2rem;
    position: relative;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    overflow: hidden;
}

/*-------------------- MEDIUM DEVICE --------------------*/
@media screen and (max-width: 1024px){
    header{
      height: auto;
    }
  }

  /*-------------------- SMALL DEVICE --------------------*/
  @media screen and (max-width: 600px){
    header{
      height: auto;
      }

    .header_socials{
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 0.8rem;
        position: relative;
        left: 37%;
        bottom: -0.2rem;
        width: 7rem;
    }
  }