.portfolio{
    /*border: 2px solid rgb(217, 255, 0);*/
    padding-top: 6rem;
}

.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
    padding-top: 2rem;
}

.portfolio_item{
    background: #4e5462;
    padding: 1.3rem;
    border-radius: 1rem;
    transition: var(--transition);
    box-shadow: 1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 8px 1px rgba(255, 220, 94, 0.7),
                1px 1px 14px 1px rgba(255, 220, 94, 0.7);
}

.portfolio_item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
    box-shadow: 0 0 20px var(--color-primary );
    transform: rotateX(-15deg);
}

.portfolio_item-image{
    border-radius: 1rem;
    padding-bottom: 0.5rem;
    overflow: hidden;
}

.portfolio_item h5{
    text-align: center;
    padding-bottom: 0.5rem;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(243, 208, 112);
}

/*-------------------- MEDIUM DEVICE --------------------*/
@media screen and (max-width: 1180px){
    .portfolio_container{
        grid-template-columns: repeat(2, 1fr);
    }
}

  /*-------------------- SMALL DEVICE --------------------*/
@media screen and (max-width: 600px){
    .portfolio{
        /*border: 2px solid rgb(217, 255, 0);*/
        padding-top: 6rem;
    }
    .portfolio_container{
        grid-template-columns: repeat(1, 1fr);
    }
}