nav{
    top: 0.8rem;
    background: linear-gradient(.180turn, #bdc3c7 ,#2c3e50 );
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    border: 1px solid #000000c7;
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}



nav a:hover{
    background: rgba(9, 9, 9, 0.527);
    color: var(--color-primary);
    box-shadow: 0px 7px 2px #fff;
    color: #fff;
    transform: translateY(-8px);
    border: 1px solid #fff;
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
    transform: translateY(-8px);
    box-shadow: 0px 7px 2px var(--color-primary);
    border: 1px solid var(--color-primary);
}