.experience{
    /*border: 2px solid rgb(217, 255, 0);*/
    padding-top: 6rem;
}

.experience_container{
    margin-bottom: 35px;
    margin-top: 2rem;
    width: 50rem;
    background: #4e5462;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    transition: var(--transition);
    text-align: center;
    font-weight: bold;
    box-shadow: 1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 8px 1px rgba(255, 220, 94, 0.7),
                1px 1px 14px 1px rgba(255, 220, 94, 0.7);
}

.experience_container h4{
    font-size: 1.5em;
    margin-bottom: 10px;
    font-weight: bold;
    text-decoration: underline;
}

.experience_container h3{
    font-weight: bold;
}

.experience_container h5{
    color: var(--color-light);
}

.experience_container:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    box-shadow: 0 0 20px var(--color-primary );
}


.experience_details{
    display: flex;
    place-items: center;
    justify-content: center;
    gap: 0.6rem;
    padding-bottom: 1rem;
}

/*-------------------- MEDIUM DEVICE --------------------*/
@media screen and (max-width: 1024px){
    .experience_container{
        width: 37rem;
    }
}

  /*-------------------- SMALL DEVICE --------------------*/
@media screen and (max-width: 600px){
    .experience{
        /*border: 2px solid rgb(217, 255, 0);*/
        padding-top: 6rem;
    }
    .experience_container{
        width: 24rem;
    }
}