.contact{
    /*border: 2px solid rgb(217, 255, 0);*/
    padding-top: 6rem;
}

.contact_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
    padding-top: 2rem;
}

.contact_option{
    background: #4e5462;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    transition: var(--transition);
    text-align: center;
    font-weight: bold;
    box-shadow: 1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 8px 1px rgba(255, 220, 94, 0.7),
                1px 1px 14px 1px rgba(255, 220, 94, 0.7);
}

.contact_icon{
    font-size: 1.4rem;
    color: rgba(255, 220, 94, 0.7);
}

.contact_container h4{
    font-weight: bold;
    color: rgba(0, 0, 0, 0.909);
}

.contact_option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    box-shadow: 0 0 20px var(--color-primary );
}

.location_container{
    display: grid;
    place-items: center;
    justify-content: center;
    gap: 0.6rem;
    padding-bottom: 1rem;
    padding-top: 3rem;
}

.location{
    display: flex;
    place-items: center;
    justify-content: center;
    gap: 0.6rem;
    padding-bottom: 1rem;
    padding-top: 3rem;
}

.location_container h4{
    background: #ffffff71;
    font-weight: bold;
    color: rgb(0, 0, 0);
}

/*-------------------- MEDIUM DEVICE --------------------*/
@media screen and (max-width: 1024px){
    .contact_container{
        grid-template-columns: repeat(2, 1fr);
    }
}

  /*-------------------- SMALL DEVICE --------------------*/
@media screen and (max-width: 600px){
    .contact{
        /*border: 2px solid rgb(217, 255, 0);*/
        padding-top: 6rem;
    }
    .contact_container{
        grid-template-columns: repeat(1, 1fr);
    }
}