.about{
    /*border: 2px solid rgb(217, 255, 0);*/
    padding-top: 6rem;
}

.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    margin-top: 2rem;
}

.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent,#fed96a6c, transparent);
    display: grid;
    place-items: center;
}

.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(3deg);
    transition: var(--transition);
    filter: blur(1px);
}

.about_me-image:hover{
    transform: rotate(0deg);
    box-shadow: 0 0 10px var(--color-white )
}

.about_cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    width: auto;
}

.about_card{
    background: #4e5462;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    box-shadow: 1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 8px 1px rgba(255, 220, 94, 0.7),
                1px 1px 14px 1px rgba(255, 220, 94, 0.7);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    box-shadow: 0 0 20px var(--color-primary);
}

.about_icon{
    color: var(--color-white);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.about_card h5{
    font-size: 1rem;
}

.about_card small{
    font-size: 0.8rem;
    color: var(--color-light);
}

.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    text-align: center;
}

.about h2{
    padding-top: 3rem;
}

.knowledge_cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    width: 60rem;
}

.knowledge_card{
    background: #4e5462;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    box-shadow: 1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 5px 0px rgba(255, 220, 94, 0.7),
                1px 1px 8px 1px rgba(255, 220, 94, 0.7),
                1px 1px 14px 1px rgba(255, 220, 94, 0.7);
}

.knowledge_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    box-shadow: 0 0 20px var(--color-primary );
}

.knowledge_card h5{
    font-size: 1.3rem;
    text-decoration: underline;
}

.knowledge_card h3{
    font-size: 1rem;
    color: #b6b6b6;
}

/*-------------------- MEDIUM DEVICE --------------------*/
@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;
        gap: 0rem;
    }

    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about_content p{
        text-align: center;
    }

    .knowledge_cards{
        grid-template-columns: repeat(2, 1fr);
        width: 40rem;
    }
}

  /*-------------------- SMALL DEVICE --------------------*/
@media screen and (max-width: 600px){
    .about{
        /*border: 2px solid rgb(217, 255, 0);*/
        padding-top: 6rem;
    }
    .knowledge_cards{
        grid-template-columns: repeat(1, 1fr);
        width: 20rem;
    }
}